import { Container } from "./styles";
import githubIcon from "../../assets/github.svg";
// import DownloadApp from '../../assets/download.png'
import externalLink from "../../assets/external-link.svg";
import ScrollAnimation from "react-animate-on-scroll";

export function Project() {


const projects = [
  {
    title: "Job Hunt and Automated Resume Parsing Web App",
    description:
      "This web application streamlines the job search process by integrating advanced features such as job hunting, automated resume parsing, and CV ranking, ensuring a seamless and user-friendly experience. Designed with a focus on functionality and a sleek user interface, it caters to both job seekers and recruiters. Leverages AI to identify missing skills and gaps in resumes, generate a personalized 3-week learning roadmap, allow HR to match candidates based on their match score with the job description, and automate cover letter generation.",
    githubLink: "https://github.com/ZaryabXProgrammer/JOB-QUEST",
    liveLink:
      "https://66620fb5b3611552903aeb03--poetic-paprenjak-c46b2b.netlify.app/",
    techStack: ["ReactJs", "NodeJs", "MongoDB", "Lottie"],
  },

  {
    title: "Bravo Creative Media Agency",
    description:
      "A modern, responsive website for Bravo Creative Media Agency, focusing on a clean, sleek user interface that reflects the agencys creative and innovative brand. The website is designed to captivate visitors with its minimalist layout while offering a seamless user experience across all devices.",

    liveLink: "https://bravo-creative-media.vercel.app/",
    techStack: ["ReactJs", "TailwindCss"],
  },
  {
    title: "DigitalM - Agency Website",
    description:
      "Cutting-edge website for Matrix M Digital Agency, delivering a clean, professional user interface that showcases their digital solutions in a modern and engaging way. The website is crafted to offer an intuitive experience while reflecting the agency's expertise in digital marketing and innovation..",
    githubLink: "https://github.com/ZaryabXProgrammer/JOB-QUEST",

    liveLink: "https://digital-m-ivory.vercel.app/",
    techStack: ["HTML", "CSS"],
  },
  {
    title: "CurrencyPH",
    description:
      "I developed a cryptocurrency website called CurrencyPH, featuring an enhanced user interface for a seamless and engaging user experience. The site integrates with smart wallets, allowing users to securely manage and transact their cryptocurrency assets. The combination of a modern, intuitive UI and robust smart wallet connectivity ensures that CurrencyPH offers both aesthetic appeal and functional excellence for cryptocurrency enthusiasts.",
    liveLink: "https://currency-ph-new.netlify.app/",
    techStack: ["ReactJs", "Styled Components", "Lottie"],
  },
  {
    title: "WearKits- eCommerce Store",
    description:
      "An innovative eCommerce platform that emphasizes modern UI/UX principles to deliver a seamless shopping experience. The design is implemented using styled-components, ensuring a consistent and responsive interface. Utlised Efficient state transfer using Redux, allowing for smooth and dynamic interactions. Implemnted secure payment processing integrated with the Stripe payment gateway.",
    githubLink: "https://github.com/ZaryabXProgrammer/E-COMMERCE-STORE---UI",
    liveLink: "https://wearkits-ecommerce-store.vercel.app/",
    techStack: ["Stripe", "ReactJs/Nodejs", "Redux"],
  },
  {
    title: "FitLabClub - Next Generation Gym Guide",
    description:
      "A fitness application that provides users with customized gym exercises based on their individual requirements. The app is powered by APIs that curate exercise routines tailored to user preferences and fitness goals, ensuring users have access to up-to-date and personalized workout plans.",
    githubLink:
      "https://github.com/ZaryabXProgrammer/Ultimate-Fitness-Club-WebApp",
    liveLink: "https://fit-lab-club.netlify.app/",
    techStack: ["ReactJs", "Custom and third-party APIs"],
  },
  {
    title: "Ultimate Bank UI Design",
    description:
      "A visually appealing bank application designed with a focus on thoughtful layout and color schemes. The design leverages React.js to create an intuitive and user-friendly interface, employing modern UI/UX principles for an attractive and functional layout.",
    githubLink: "https://github.com/ZaryabXProgrammer/Bank-App",
    liveLink: "https://hoobank-two-tawny.vercel.app/",
    techStack: ["React", "Gradients", "CSS"],
  },
  {
    title: "DailyPulse News App",
    description:
      "A web application that keeps users informed with the latest news articles and headlines from various sources. The app aggregates news to provide a comprehensive and up-to-date feed, ensuring users have access to the most current information available.",
    githubLink: "https://github.com/ZaryabXProgrammer/Daily-Pulse---React-App",
    techStack: ["React", "NewsAPI"],
  },
  {
    title: "Microsoft Homepage Clone",
    description:
      "A meticulous clone of the Microsoft homepage, designed to emulate the original site's aesthetics. This project showcases attention to detail and proficiency in frontend development, resulting in a visually appealing and responsive web application.",
    githubLink: "https://github.com/ZaryabXProgrammer/Microsoft-Clone",
    techStack: ["React", "Typescript", "Html", "css"],
  },
];

  return (
    <Container id="project">
      <h2>My Projects</h2>
      <div className="projects">
        {projects.map((project, index) => (
          <ScrollAnimation animateIn="flipInX" key={index}>
            <div className="project">
              <header>
                <svg
                  width="50"
                  xmlns="http://www.w3.org/2000/svg"
                  role="img"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="#23ce6b"
                  strokeWidth="1"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                >
                  <title>Folder</title>
                  <path d="M22 19a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h5l2 3h9a2 2 0 0 1 2 2z"></path>
                </svg>
                <div className="project-links">
                  {project.githubLink && (
                    <a
                      href={project.githubLink}
                      target="_blank"
                      rel="noreferrer"
                    >
                      <img src={githubIcon} alt="Visit GitHub repository" />
                    </a>
                  )}
                  {project.liveLink && (
                    <a href={project.liveLink} target="_blank" rel="noreferrer">
                      <img src={externalLink} alt="Visit live site" />
                    </a>
                  )}
                </div>
              </header>
              <div className="body">
                <h3>{project.title}</h3>
                <p>{project.description}</p>
              </div>
              <footer>
                <ul className="tech-list">
                  {project.techStack.map((tech, techIndex) => (
                    <li key={techIndex}>{tech}</li>
                  ))}
                </ul>
              </footer>
            </div>
          </ScrollAnimation>
        ))}
      </div>
    </Container>
  );
}
